.headerWrap { position:fixed; top:0; left:0; width:100%; 
    z-index:99; color:#fff; 
    .row { 
        position:relative;
        display:flex; height:80px; justify-content:space-between;
        align-items: center;
        .logo, .nav, .mobileNav { align-self:center }
        .mobileNav { position:relative;
            button { padding:10px 20px; border-radius:50px;
                border:none; font-size:20px; display:flex;
                cursor:pointer; 
                .ham { margin-right:10px }       
                .ham, .person { align-self:center  }   
            }
            .mobileNavList { display:none;
                position:absolute; top:100%; right:0; 
                margin-top:10px;
                background:#fff; padding:10px 0px; 
                color:#000; width:150px; 
                border-radius:10px; 
                &.opener { display:block }
                ul {
                    li { line-height:30px; padding:0 20px }
                    li:nth-child(3) { 
                        border-top:1px solid #ddd; padding-top:10px;
                        margin-top:10px; 
                    }
                }
            }
        }
        .nav { position:absolute; left:50%; transform:translate(-50%);
           .depth1 { 
               li { position:relative; padding:15px;
                cursor:pointer; display: inline-block;
            }
           }
          .depth2 { display:none;
              position:absolute; top:100%; left:0;
              width:100px; background:#fff; color:#000;
            &.on { display:block }
          }
        }
    }
}